<template>
  <div id="root">
    <span style="color: white">Oneself - 管理系统</span>
    <a-badge style="float: right">
      <a-avatar shape="square" icon="user"/>
    </a-badge>
  </div>
</template>

<script>
  import {Badge, Avatar} from 'ant-design-vue'

  export default {
    name: "TopContent",
    components: {
      [Badge.name]: Badge,
      [Avatar.name]: Avatar
    }
  }
</script>

<style scoped>
  #root {
    display: flex;

  }

  span {
  }
</style>



